@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;0,900;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  
}

input {
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400; 
}

button.btn.btn-primary, .btn-primary:active, .btn-primary:visited {
  background-color: rgb(92, 106, 131);
  border-color: black;
  margin-right: 5px;
  margin-top: 10px;
  
}

button.btn-primary:hover {
  background-color: black;
  border-color: black;
}

button.btn.btn-secondary {
  margin-top: 10px;
}

button {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
  margin-bottom: 10px;
  border-radius: 28px;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
}

.login-form {
  margin-top: 50px;
  display: flex; 
  flex-flow: column wrap; 
  width: 20%;
  margin-left: 38%;
  padding: 30px;
  background-color: rgb(146, 169, 211);
}

.signup-form {
  margin-top: 50px;
  display: flex; 
  flex-flow: column wrap; 
  width: 20%;
  margin-left: 38%;
  padding: 30px;
  background-color: rgb(146, 169, 211);
}

.navbar-container {
  background-color: rgb(146, 169, 211);
  display: flex;
  flex-direction: column;
  min-height: 300vh;
  width: 15%;
}

.avatar {
  max-width: 90%;
  border-radius: 28px;
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
  margin-bottom: 30px;
  
}

.nav-button {
  width: 100%;
  padding: 2px;
  margin-top: 10px;
}

.dashboard-container {
  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: auto;
  width: 85%;
  right: 0px;
  top: 0px;
  position: absolute;
  
}

.add-new-feature-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 80%;
  right: 0px;
  top: 0px;
  position: absolute; 
}

.feature-card {
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
  width: 25%;
  margin: 25px;
  border-radius: 28px;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
  padding-top: 20px;
  padding-bottom: 25px;
}

.feature-image {
  width: 80%;
  border-radius: 28px;
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
}

.submit-share-button {
  margin-top: 10px;
}

.feature-form {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-top: 10px;
  margin-left: 25%;
}

.feature-form-inputs {
  margin-bottom: 20px;
  height: 20px;
}

.resource-container {
  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 105vh;
  width: 85%;
  right: 0px;
  top: 0px;
  position: absolute;
}

.update-feature-container {
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
  width: 54%;
  margin-top: 30px;
  border-radius: 28px;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
}

.messages-container {
  border-width: 2px;
  border-color: rgb(8, 8, 8);
  border-style: solid;
  width: 34%;
  margin-top: 30px;
  border-radius: 28px;
  box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.2);
}

.message-block {
  height: 50%;
  overflow-y: scroll;
  text-align: left;
  padding-left: 40px;
}


.footer-sources {
  padding-top: 500px;
}

